const axios = require("axios").default;
class Words {
    constructor() {
        this.baseUrl = new URL("https://rs-learnwords-example.herokuapp.com/");
        this.getWords = async (wordLocation) => {
            const url = new URL("words", this.baseUrl);
            url.searchParams.set("group", String(wordLocation.group));
            url.searchParams.set("page", String(wordLocation.page));
            const response = await axios.get(url.href, {
                headers: {
                    Accept: "application/json",
                },
            });
            const content = await response.data;
            return content;
        };
        this.getWordByWordId = async ({ wordId }) => {
            const url = new URL(`words/${wordId}`, this.baseUrl);
            const response = await axios.get(url.href, {
                headers: {
                    Accept: "application/json",
                },
            });
            const content = await response.data;
            return content;
        };
    }
}
const words = new Words();
exports.default = words;
